import React, {useEffect, useState} from 'react';
import {v4 as uuidv4} from 'uuid';


export default function TaskForm({tasks, setTasks, viewTask, setViewTask}) {

    const [task, setTask] = useState({title: '', body: ''});
    const [editMode, setEditMode] = useState(false);

    useEffect(() => {

        if (viewTask) {

            setTask(viewTask)
        }

    }, [viewTask])

    const formSubmit = e => {
        e.preventDefault();

        if (editMode) {


            let newTasks = [...tasks];

            newTasks = newTasks.map(t => {

                if (t.id === viewTask.id) {

                    t.title = task.title;
                    t.body = task.body;
                }

                return t;
            })


            setTasks(newTasks);


        } else {


            task.id = uuidv4();
            task.date = new Date();

            setTasks([task, ...tasks]);

            setTask({title: '', body: ''});
        }

    };

    const handleChange = e => {

        const newTask = {...task};
        newTask[e.target.name] = e.target.value;

        setTask(newTask)

    };

    if (viewTask && !editMode) {

        return (

            <div className="mx-auto card text-white bg-dark mb-3"
                 style={{width: '100%', minHeight: '400px'}}>


                <div className="card-header">Task Details</div>
                <div className="card-body">

                    <h5 className="card-title">{viewTask.title}</h5>


                    <br/>
                    <p className="card-text">{viewTask.body}</p>
                </div>
                <div className="card-footer">

                    <div className="btn-group" role="group">
                        <button type="button" onClick={() => {
                            setEditMode(true)
                        }} className="btn btn-info">Edit
                        </button>
                        <button type="button" onClick={() => {
                            setViewTask(null);
                            setTask({title: '', body: ''});
                            setEditMode(false)
                        }} className="btn btn-secondary">Cancel
                        </button>
                    </div>
                </div>

            </div>
        );

    }

    return (


        <div className="mx-auto card text-white bg-dark mb-3"
             style={{width: '100%', minHeight: '400px'}}>
            <form onSubmit={formSubmit}>

                <div className="card-header">{editMode ? 'Edit Task' : 'Add New Task'}</div>
                <div className="card-body">


                    <input type="text" name={"title"} value={task.title} className={"form-control"}
                           onChange={handleChange}
                           placeholder={"Title (example: buy grocery, pay bill etc)"} required/>

                    <br/>
                    <textarea name="body" cols="30" rows="7" className={"form-control"}
                              placeholder={"Task Info"} value={task.body} onChange={handleChange}/>
                </div>
                <div className="card-footer">

                    <div className="btn-group" role="group">
                        <button type="submit" className="btn btn-success">Save</button>
                        <button type="button" onClick={() => {
                            setViewTask(null);
                            setTask({title: '', body: ''});
                            setEditMode(false)
                        }} className="btn btn-secondary">Cancel
                        </button>
                    </div>
                </div>
            </form>
        </div>
    );
}
