import React, {useEffect, useState} from 'react';

import moment from 'moment';

export default function ({tasks, setViewTask, viewTask, setTasks}) {



    const [cls, setCls] = useState();

    useEffect(() => {

        let newTasks = [...tasks];

        newTasks = newTasks.map(task => {

            if (viewTask && task.id === viewTask.id) {
                task.active = true;
            } else {
                task.active = false;
            }

            return task;
        })

        setTasks(newTasks);
    }, [viewTask])

    useEffect(()=>{

        // localStorage.setItem('list', JSON.stringify(tasks))

    },[tasks])

    return (
        <ul className="list-group">

            {tasks.map((task, i) => {

                let cls = 'list-group-item  d-flex justify-content-between align-items-center';

                if (task.active) {
                    cls = 'list-group-item list-group-item-dark d-flex justify-content-between align-items-center';
                }

                return (
                    <li key={Math.random()} onClick={() => {
                        setViewTask(task)
                    }}
                        className={cls}
                    >
                        <span className="badge badge-primary badge-pill">{moment(task.date).format("MMM Do YY")}</span>
                        {task.title}
                    </li>
                )
            })}
        </ul>
    );
}